#A {
  color: white;
  background: rgb(56, 76, 214);
  background: linear-gradient(
    180deg,
    rgba(56, 76, 214, 1) 14%,
    rgba(95, 158, 214, 1) 57%,
    rgba(3, 175, 219, 1) 100%
  );
}

#B {
  color: white;
  background: rgb(245, 132, 241);
  background: linear-gradient(
    261deg,
    rgba(245, 132, 241, 1) 19%,
    rgba(216, 106, 237, 1) 46%,
    rgba(184, 78, 233, 1) 82%
  );
}
#C {
  color: white;
  background: rgb(182, 12, 57);
  background: linear-gradient(
    300deg,
    rgba(182, 12, 57, 1) 24%,
    rgba(181, 48, 100, 1) 65%,
    rgba(224, 115, 175, 1) 100%
  );
}
#D {
  color: white;
  background: rgb(145, 48, 38);
  background: radial-gradient(
    circle,
    rgba(145, 48, 38, 1) 9%,
    rgba(190, 86, 48, 1) 29%,
    rgba(255, 197, 145, 1) 100%
  );
}
#E {
  color: white;
  background: rgb(23, 75, 64);
  background: radial-gradient(
    circle,
    rgba(23, 75, 64, 1) 21%,
    rgba(13, 111, 77, 1) 57%,
    rgba(78, 240, 183, 1) 100%
  );
}

