@media (min-width: 580px) and(max-width: 900px) {
  .data {
    display: grid;
    justify-content: center;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .card-title {
    font-size: 8px;
  }
  .card-text {
    font-size: 5px;
  }
  
}

@media (max-width: 580px) {
  .data{
    display: grid;
    grid-template-columns: repeat(1, 1fr);;
  }
  .card-title {
    font-size: 8px;
  }
  .card-text {
    font-size: 5px;
  }
  .search{
    margin-top: 10%;
  }
  #pais{
    text-align: center;
  }
}
  @media (max-width: 280px) {
    .data{
      display: grid;
      grid-template-columns: repeat(1, 1fr);;
    }
    .card-title {
      font-size: 8px;
    }
    .card-text {
      font-size: 5px;
    }
    .search{
      margin-top: 20%;
    }
    #pais{
      text-align: center;
    }
  }

